<script setup lang="ts">
import {DateTime} from 'luxon';

interface Props {
  source: string;
  time: string;
  sender: string;
  text: string;
}

const roleClasses: Record<string, string> = {
  customer: 'customer-message',
  moder: 'moder-message',
  system: 'system-message',
};

const props = defineProps<Props>();
const localTime = DateTime.fromISO(props.time, {zone: 'utc'})
  .toLocal()
  .toFormat('yyyy-MM-dd hh:mm:ss', {});
</script>

<template>
  <div
    class="message"
    :class="roleClasses[props.source]"
  >
    <div class="message-header">
      <span class="sender">{{ props.sender }}</span>
      <span class="timestamp">{{ localTime }}</span>
    </div>
    <div class="message-body">
      {{ props.text }}
    </div>
  </div>
</template>

<style scoped lang="scss">
.message {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 5px;
}

.message-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.sender {
  font-weight: bold;
}

.timestamp {
  font-size: 12px;
}

.message-body {
  padding: 8px;
  border-radius: 5px;
}

.customer-message {
  background-color: hsla(40, 100%, 30%, 1);
}

.moder-message {
  background-color: hsla(90, 100%, 30%, 1);
}

.system-message {
  background-color: hsla(180, 100%, 30%, 1);
  text-align: center;
  font-style: italic;
}
</style>
